<template>
    <transition name="fade">
        <div
            v-show="show"
            v-bind:class="[{'app-mask-show': show}, 'app-mask-wrapper']"
            @click.stop="closeAppMask">
        </div>
    </transition>
</template>

<script>
export default {
    props: ['show'],
    methods: {
        closeAppMask() {
            this.$emit('close-mask');
        }
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: #4DBA87
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom



.app-mask-wrapper
    width 100%
    height 100%
    z-index $app-mask-zindex
    background $app-mask-bgcolor
    opacity $app-mask-opacity
    position fixed
    top 0
    left 0

    &.fade-enter,
    &.fade-leave-active
        opacity 0
        transition opacity $app-mask-duration

    &.fade-leave,
    &.fade-enter-active
        transition opacity $app-mask-duration

</style>



